<template>
  <div>
    <div style="margin-top: 20px; margin-bottom: 20px">
      <label for="userAgent">userAgent:</label>
      <span id="userAgent" v-text="device"></span>
    </div>
    <div>
      <label for="touchstart">touchstart:</label>
      <span id="touchstart" v-text="touchstart"></span>
    </div>
     <div>
      <label for="touchstart">touchmove:</label>
      <span id="touchstart" v-text="touchmove"></span>
    </div>
    <div>
      <label for="touchend">touchend:</label>
      <span id="touchend" v-text="touchend"></span>
    </div>
    <div>
      <label for="click">click:</label>
      <span id="click" v-text="clickText"></span>
    </div>
    <div>
      <label for="diffClickTouchend">diff click - touchend:</label>
      <span id="diffClickTouchend" v-text="diffClickTouchend"></span>
    </div>
    <div class="flex-box">
      <van-button type="primary" @click="handleClean">clean</van-button>
      <van-button type="primary" @click="handleDiff">diff</van-button>
    </div>
     <div
        class="primary"
        @click="handleClick"
        @touchstart="handleTouchStart"
        @touchmove="handleTouchmove"
        @touchend="handleTouchend"
        >
         <span>test</span>
        </div>
  </div>
</template>
<script>
export default {
  name: "ClickDelay",
  data() {
    return {
      device: "",
      clickText: "",
      touchstart: "",
      touchend: "",
      diffClickTouchend: "",
      touchmove:''
    };
  },
  created() {
    this.device = window.navigator.userAgent;
  },
  methods: {
    handleClick() {
      this.clickText = Date.now();
    },
    handleTouchStart() {
      this.touchstart = Date.now();
    },
    handleTouchmove(){
        this.touchmove = Date.now()
    },
    handleClean() {
        this.clickText = ''
        this.touchstart = ''
        this.touchend = ''
        this.diffClickTouchend = ''
    },
    handleTouchend() {
      this.touchend = Date.now();
      console.log(this.touchend);
    },
    handleDiff(){
        this.diffClickTouchend = this.clickText-this.touchend
    }
  },
};
</script>
<style scoped>
.flex-box {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.primary{
    height: 40px;
    width: 100%;
    text-align: center;
